@import url(https://fonts.googleapis.com/css2?family=Spicy+Rice&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
    text-align: center;
    margin-top: 5vh;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrapper {
    background-color: black;
    display: flex;
    justify-content:space-around;
    
    }

.nav-wrapper {
    display:flex;
    justify-content: flex-end;
    padding: 10px 20px;
    margin-right: 15px;
    margin-top: 15px;
}

.sitename {
    font-family: 'Spicy Rice', cursive;
    color: white;
    font-size: 2.25rem;
    align-self: center;
    margin-top: 25px;
}

li {
    
    list-style: none;
    color:white;
    font-size:1rem;
    margin: 13px;
    width: auto;
}

.logo {
    padding: 10px;
    align-self: center;
}

a:link {
    text-decoration: none;
}
.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  .container {
    display:flex;
    flex-flow: row;
    margin: 0 auto;
    border: 1px solid #e6e4e4;
    width: 150px;
    margin-bottom: 15px;
    height: 40px;
    margin-top: 15px;
    
  }
  
  h3 {
    margin-top: 50px;
  }
  .btn__input {
    text-align: center;
    border: none;
    background: transparent;
    font-size: 12px;
    width: 50%;
  }
  
  .btn__decrement {
    border: none;
    background: transparent;
    padding: 10px;
    margin-left: 10px;
    font-size: 15px;
    color: rgb(71, 71, 252);
    
  }
  
  .btn__increment {
    border: none;
    background: transparent;
    padding: 5px;
    margin-right: 10px;
    font-size: 15px;
    color: rgb(71, 71, 252);
  }
  
  span .innerbutton {
    color: rgb(71, 71, 252);
    
  }
  
  .btn__carrito {
    background: transparent;
    padding: 10px 10px;
    color: rgb(71, 71, 252);
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__carrito-submit {
    background: #4545fc;
    padding: 10px 10px;
    color: white;
    border: none;
    /* outline: rgb(71, 71, 252) solid 1px; */
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__carrito-submit:hover {
    background-color: rgb(40, 40, 139);
    color: white;
    padding: 10px 10px;
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__checkout {
    background: rgb(71, 71, 252);
    padding: 10px 10px;
    color: white;
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__checkout:hover {
    background-color: white;
    color: rgb(71, 71, 252);
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
  }

  .btn__container, h3 {
    text-align: center;
  }
  
  .btn__carrito:hover {
    background-color: rgb(71, 71, 252);
    color: white;
  }

.cardcontainer {
    text-align: center;
    margin: 35px;
    padding: 15px;
    width: 350px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

}

.itemContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}


