.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  .container {
    display:flex;
    flex-flow: row;
    margin: 0 auto;
    border: 1px solid #e6e4e4;
    width: 150px;
    margin-bottom: 15px;
    height: 40px;
    margin-top: 15px;
    
  }
  
  h3 {
    margin-top: 50px;
  }
  .btn__input {
    text-align: center;
    border: none;
    background: transparent;
    font-size: 12px;
    width: 50%;
  }
  
  .btn__decrement {
    border: none;
    background: transparent;
    padding: 10px;
    margin-left: 10px;
    font-size: 15px;
    color: rgb(71, 71, 252);
    
  }
  
  .btn__increment {
    border: none;
    background: transparent;
    padding: 5px;
    margin-right: 10px;
    font-size: 15px;
    color: rgb(71, 71, 252);
  }
  
  span .innerbutton {
    color: rgb(71, 71, 252);
    
  }
  
  .btn__carrito {
    background: transparent;
    padding: 10px 10px;
    color: rgb(71, 71, 252);
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__carrito-submit {
    background: #4545fc;
    padding: 10px 10px;
    color: white;
    border: none;
    /* outline: rgb(71, 71, 252) solid 1px; */
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__carrito-submit:hover {
    background-color: rgb(40, 40, 139);
    color: white;
    padding: 10px 10px;
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__checkout {
    background: rgb(71, 71, 252);
    padding: 10px 10px;
    color: white;
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
    transition: all  ease-in 0.2s;
    text-align: center;
    margin-bottom: 15px;
  }

  .btn__checkout:hover {
    background-color: white;
    color: rgb(71, 71, 252);
    border: none;
    outline: rgb(71, 71, 252) solid 1px;
  }

  .btn__container, h3 {
    text-align: center;
  }
  
  .btn__carrito:hover {
    background-color: rgb(71, 71, 252);
    color: white;
  }
