
@import url('https://fonts.googleapis.com/css2?family=Spicy+Rice&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrapper {
    background-color: black;
    display: flex;
    justify-content:space-around;
    
    }

.nav-wrapper {
